header {
  height: 130vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header_container {
  text-align: center;
  /* height: 100%; */
  position: relative;
} 

/* =================== Resume =================== */
.resume {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* =================== Header Socials =================== */
.header_socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  /* left: 0; */
  /* bottom: 3rem; */
  left: -3rem;
  bottom: 6rem;
}

.header_socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/* =================== Me =================== */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 15rem;
  /* height: 100%; */
  position: absolute;
  left: calc(50% - 7rem);
  margin-top: 4rem; 
  border-radius: 50rem 50rem 0rem 0rem;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* =================== Scroll Down =================== */
.scroll_down {
  position: absolute;
  /* right: -2.3rem; */
  /* bottom: 5rem; */
  right: -5rem;
  bottom: 8.5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* =================== Media Queries (Medium Devices) =================== */
@media screen and (max-width: 1024px) {
  header {
    height: 130vh;
  }
}

/* =================== Media Queries (Small Devices) =================== */
@media screen and (max-width: 600px) {
  header {
    height: 130vh;
  }

  .header_socials, .scroll_down {
    display: none;
  }
}